
import React from 'react';
import './App.css';
import Axios from 'axios';


class MyForm extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      username:'',
      age:null,
      employeeList:[]
    };
  }

getEmployee = () => {
  Axios.get('http://localhost:3001/employees').then((response) => {
     console.log(response);
     this.setState({employeeList:response.data})
   });
};

addEmployee = () => {
  console.log(this.state.username);
  Axios.post('http://localhost:3001/create', {
   username: this.state.username,
  age:this.state.age
  }).then(() => {
    console.log("success");

  //   this.setState({employeeList:[...this.state.employeeList, {
  //     username: this.state.username,
  //    age:this.state.age
  //    },
  // ]});

  });
};


myChangeHandler = (event) => {
  let nam = event.target.name;
  let val = event.target.value;
  this.setState({[nam]:val });
}

displyaInfo = () => {
    console.log(this.state.username + this.state.age);
}

render() {
  return (
    <div className="App">

<h2>Hello {this.state.username} {this.state.age}</h2>

<label>Name</label><br />
<input type="text" name='username' onChange={this.myChangeHandler} /> <br />

<label>age</label><br />
<input type="number" name='age' onChange={this.myChangeHandler} /> <br />

<button onClick={this.addEmployee}>Submit</button>
     <hr />
     <button onClick={this.getEmployee}>Show Employees</button>
     <br />
     {this.state.employeeList.map((val, key) => {
       return <div> {val.name} </div>
     })}
    </div>
  );
}

}



export default MyForm;
